.home{
    margin: 40px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    width: 50%;
    height: 450px;
    position: relative;
    top: 100px;
    border-radius: 20px;
    box-shadow:3px 3px 3px #05002b , -3px -3px 3px #04284d; 
}
.title{
    position: absolute;
    top: 10px;
    font-size: 25px;
    color: #fff;

}
.err{
    background-color: rgb(243, 8, 8);
    margin: 20px;
    position: absolute;
    top: 30px;
    padding: 10px 20px;
    color: #fff;
    width: 60%;
    text-align: center;
}
.input input{
    padding: 10px 20px;
    width: 400px;
    margin:100px auto 10px auto;
}
.home-category{
    text-align: center;
    margin: 10px auto;
    color: #fff;
}
.select-category{
    padding: 10px 20px;
    width: 400px;
    margin-top: 8px;
}
.difficulty{
    text-align: center;
    margin: 10px auto;
    color: #fff;
}
.select-difficulty{
    width: 400px;
    padding: 10px 20px;
    margin-top: 8px;
}

.home-button{
    margin-top: 7px;
    padding: 10px 20px;
    border: none;
    background: #780206; 
background: -webkit-linear-gradient(to right, #061161, #780206); 
background: linear-gradient(to right, #061161, #780206);
color: #fff;
cursor: pointer;
border-radius: 6px;
box-shadow:3px 3px 3px #05002b , -3px -3px 3px #04284d; 
font-size: 20px;
}



/*-------------media------------*/
@media(max-width:768px){
    .home{
        width: 80%;
    }
}

@media(max-width:508px){
    .home{
        width: 88%;
    }
    .input input{
                width: 250px;
    }
    .select-category{
        width: 250px;
    }
    .select-difficulty{
        width: 250px;
    }
}